import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
class Header extends Component {  

constructor(props){
    super(props);

    this.navigate = this.navigate.bind(this);
    }

navigate(){
    this.props.history.push(process.env.PUBLIC_URL+'/abc')
    }
  render() {      
    console.log("props nya=");
    console.log(process.env.PUBLIC_URL);
    return (
        <div>
        <h2>Headers</h2>
        <ul>
            <li><Link to={`${process.env.PUBLIC_URL}/`}>Home</Link></li>
            <li><Link to={`${process.env.PUBLIC_URL}/homeChild`}>Home Child</Link></li>
            <li><Link to={`${process.env.PUBLIC_URL}/user`}>User</Link></li>
            <li><Link to={`${process.env.PUBLIC_URL}/archive/1`}>Archive</Link></li>
            <button onClick={this.navigate}>button to home</button>
            <li><Link to={`${process.env.PUBLIC_URL}/todo`}>Todo</Link></li>
            <li><Link to={`${process.env.PUBLIC_URL}/favorite`}>Favorite</Link></li>
            <li><Link to={`${process.env.PUBLIC_URL}/setting`}>Setting</Link></li>
        </ul>

            <hr/>
        </div>
    );
  }
}

export default withRouter(Header);



